export default {
	data() {
		return {
			finish:false,
			club_id:'',
			clubData:{},
			content:''
		}
	},
	onLoad(e) {
		uni.showLoading({
		    title: '加载中',
			mask:"true"
		});
		// console.log(e)
		this.club_id = e.id
		this.getHome()
	},
	methods: {
		commit(){
			if(!this.content){
				uni.showToast({
					title:"请填写加入申请",
					mask:'true',
					icon:"none"
				})
			}else{
				this.$api.join_club_info_v2({
					token: uni.getStorageSync("token"),
					user_id: uni.getStorageSync("userId"),
					club_id:this.club_id,
					content:this.content
				}).then(res=>{
					uni.showToast({
						title:res.data.msg,
						mask:'true',
						icon:"none"
					})
					if(res.data.code == '200'){
						uni.reLaunch({
							url:'../../pagesTwo/Club/Club'
						})
					}
				})
			}
		},
		getHome(){
			this.$api.club_homepage({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				club_id:this.club_id
			}).then(res=>{
				uni.hideLoading();
				this.clubData = res.data.data
				this.finish = true
				// console.log(this.clubData)
			})
		},
		
	}
}
